import React from 'react';
import './Style.css';

class Settings extends React.Component {
  constructor() {
    super();
    this.handleCLick = this.handleCLick.bind(this);
  }

  async handleCLick(...value) {
    const { history } = this.props;
    const API_BASE_URL = `https://opentdb.com/api.php?amount=${value.qtd}&category=${value.category}&difficulty=${value.difficulty}&type=${value.type}`;
    const response = await fetch(API_BASE_URL);
    const data = response.json();

    history.push('/game');
  }

  render() {
    return (
      <>
        <h1 data-testid="settings-title">Settings</h1>
        <form className="gameContainer settingsContainer">
          <label htmlFor="qtd">
            Number of Questions:
          </label>
          <input type="number" id="qtd" name="qtd" max="50" />
          <label htmlFor="category">
            Select Category:
          </label>
            <select id="category" name="category">
              <option className="optionStyle" value="0">Any Category</option>
              <option className="optionStyle" value="9">General Knowledge</option>
              <option className="optionStyle" value="10">Entertainment: Books</option>
              <option className="optionStyle" value="11">Entertainment: Film</option>
              <option className="optionStyle" value="12">Entertainment: Music</option>
              <option className="optionStyle" value="13">Entertainment: Musicals &amp; Theatres</option>
              <option className="optionStyle" value="14">Entertainment: Television</option>
              <option className="optionStyle" value="15">Entertainment: Video Games</option>
              <option className="optionStyle" value="16">Entertainment: Board Games</option>
              <option className="optionStyle" value="17">Science &amp; Nature</option>
              <option className="optionStyle" value="18">Science: Computers</option>
              <option className="optionStyle" value="19">Science: Mathematics</option>
              <option className="optionStyle" value="20">Mythology</option>
              <option className="optionStyle" value="21">Sports</option>
              <option className="optionStyle" value="22">Geography</option>
              <option className="optionStyle" value="23">History</option>
              <option className="optionStyle" value="24">Politics</option>
              <option className="optionStyle" value="25">Art</option>
              <option className="optionStyle" value="26">Celebrities</option>
              <option className="optionStyle" value="27">Animals</option>
              <option className="optionStyle" value="28">Vehicles</option>
              <option className="optionStyle" value="29">Entertainment: Comics</option>
              <option className="optionStyle" value="30">Science: Gadgets</option>
              <option className="optionStyle" value="31">Entertainment: Japanese Anime &amp; Manga</option>
              <option className="optionStyle" value="32">Entertainment: Cartoon &amp; Animations</option>
            </select>
          <label htmlFor="difficulty">
            Select Difficulty:
          </label>
            <select id="difficulty" name="difficulty">
              <option className="optionStyle" value="0">Any Difficulty</option>
              <option className="optionStyle" value="easy">Easy</option>
              <option className="optionStyle" value="medium">Medium</option>
              <option className="optionStyle" value="hard">Hard</option>
            </select>
          <label htmlFor="type">
            Select Type:
          </label>
            <select id="type" name="type">
              <option className="optionStyle" value="0">Any Type</option>
              <option className="optionStyle" value="multiple">Multiple Choice</option>
              <option className="optionStyle" value="boolean">True / False</option>
            </select>
          <button type="button" className="answer" onClick={ this.handleCLick }>Send</button>
        </form>
      </>
    );
  }
}

export default Settings;
